<template>
    <div class="photo_block">
        <el-image v-for="(img,index) in imgArr"
            style="width: 35vh; height: 35vh" 
            :key="index"
            @click="showImg(index)"
            :src="img" alt=""
            fit="cover"
            lazy
        />
    </div>
</template>

<script>
    export default {
        name: "pic-waterfall",
        props:['image_array'],
        data(){
            return {
                imgArr:this.image_array,
                ImgWidth:'',
                ImgCol:5,
                ImgRight:10,
                ImgBottom:10,
                deviationHeight:[],
                imgList:[],
                screenWidth:document.body.clientWidth,
            }
        },
        methods:{
            showImg(index) {
                this.$hevueImgPreview({
                    multiple: true,
                    nowImgIndex: index,
                    imgList: this.imgArr,
                    clickMaskCLose: true,
                })
            },
        }
    }
</script>

<style scoped>
    .photo_block {
        display: flex;
        justify-content: center;
        align-content: center;
        align-items: center;
        flex-wrap: wrap;
    }

    .el-image {
        /* box-shadow: 4px 4px rgba(87, 87, 87, 0.349); */
        border-radius: .5em;
        margin: 3vh 3vh;
        width: 200px;
    }
</style>
